import React from "react";
import Tabletop from 'tabletop';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  componentDidMount() {
    setTimeout(function () {
      this.props.loadingBarAdd(20);
    }.bind(this),100);
    Tabletop.init({
      key: '1CNnSjBUnSRoT_dW5aJwKmzbWqf3_80PhM9RNBMYfgM8',
      callback: googleData => {
        this.setState({
          data: googleData
        });
        this.props.loadingBarComplete();
      },
      simpleSheet: true
    })
  }

  render() {
    const { data } = this.state
    return (
      <div onMouseMove={this.mousemove} onTouchMove={this.touchmove}>
        <div className="homeWp">

          <Info />

        <div id="employee-details">
          {
            data.map(obj => {
              return (
                <div key={obj.title}>
                  <p>{obj.title}</p>
                  <p>{obj.content}</p>
                </div>
              )
            })
          }
        </div>
        </div>
      </div>
    );
  }
}

function Info() {
  return (
    <div>
    </div>
  );
}

export default Home;
